import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import  { Redirect } from 'react-router-dom';
import io from "socket.io-client";
import './../styles/Chat.css';

class Chat extends Component {

  constructor(props) {
    super(props);
    this.state = {
      user : null, //this.props.location.state.user,
      messageSent : "",
      messages : ["HELLO", "OH HII"]
    }
    this.socket = io('localhost:3000/');
  }

  handleChange(event) {
    this.setState({messageSent: event.target.value});
  }

  handleSubmit(event) {
    alert(this.state.messageSent);
  }

  render() {

    const userLists = (
      <div className="user-lists">
        <p> c </p>
      </div>
    );

    const messages = []    
    for (var i = 0; i < this.state.messages.length; i++ ){
      messages.push(<div className="msg"> {this.state.messages[i]} </div>);
    }

    const displayMessages = (
      <div className="display-messages" >  
        {messages}
      </div>
    );

    const takeInputs = (
      <form className="enter-messages" onSubmit={(event) => this.handleSubmit(event)}>
        <input className="userInput" type="text" placeholder="type new message" 
          name={this.state.messageSent} onChange={(event) => this.handleChange(event)}/>
        <input className="send" type="submit" value="Send" />
      </form>
      
    );

    return (
      <div className="chat-container">
        <div className="left-component">
          {userLists}
        </div>
        <div className="right-component">
          {displayMessages}
          {takeInputs}
        </div>
      </div>
    );
  }
}


export default Chat;



